a sociedad, creando una percepción positiva y aumentando la fidelidad hacia la marca. Este tipo de iniciativas también contribuye a mejorar el clima organizacional, puesto que los empleados se sienten más motivados y comprometidos cuando trabajan para una empresa que se preocupa por el impacto positivo en la comunidad y el medio ambiente.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-5 d-none d-xl-block",attrs:{"data-aos":"fade-right"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-19.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-12 col-xl-7",attrs:{"data-aos":"fade-left"}},[_c('h2',[_vm._v("Ética empresarial")]),_c('span',[_vm._v("La ética empresarial es un componente fundamental en el compromiso de una organización con la responsabilidad social. Las empresas que integran una perspectiva ética en sus operaciones, establecen principios claros de transparencia, justicia y respeto, asegurando que todas sus actividades estén alineadas con valores morales. Esto incluye el cumplimiento de leyes y regulaciones, pero va más allá, abarcando aspectos como la justicia en el trato a los empleados, la honestidad con los consumidores y el respeto a las comunidades. La ética empresarial también involucra la adopción de políticas de gobernanza corporativa, que eviten prácticas de corrupción y promuevan una toma de decisiones equitativa y democrática, dentro de la organización. Las empresas que se comprometen con la ética empresarial suelen implementar códigos de conducta y crear comités de ética que supervisan el cumplimiento de estas normativas y promueven una cultura de responsabilidad y respeto. (International Organization for Standardization [ISO], 2019)")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-xl-7",attrs:{"data-aos":"fade-right"}},[_c('span',[_vm._v("La economía del bien común, es otro enfoque emergente en el compromiso ético y social de las empresas, impulsado por la idea de que las organizaciones no solo deben maximizar sus beneficios financieros, sino también contribuir al bienestar de la sociedad y al desarrollo sostenible. Este modelo, propuesto por el economista Christian Felber, sugiere que el éxito de una empresa debe medirse en términos de su impacto positivo en la sociedad, el medio ambiente y los derechos humanos. Las empresas que adoptan este modelo desarrollan balances del bien común, documentos en los que evalúan sus prácticas en relación con criterios de justicia social, sostenibilidad y participación democrática. Este enfoque implica una redefinición de los valores empresariales, enfocándose en cómo la empresa contribuye al bien común en lugar de maximizar las ganancias para sus accionistas. En países como Alemania y Austria, la economía del bien común ha ganado popularidad, especialmente en sectores como la banca ética y el comercio justo, donde se priorizan prácticas responsables y justas.")])]),_c('div',{staticClass:"col-5 d-none d-xl-block",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-20.png"),"alt":"Texto que describa la imagen"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-xl-7",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"bg-azul p-4 mb-4"},[_c('span',[_vm._v("En una encuesta reciente, un porcentaje significativo de consumidores afirmó que estarían dispuestos a pagar más por productos de empresas que demuestran un compromiso genuino con la responsabilidad social y la ética. Además, esta imagen positiva ayuda a atraer y retener talento, especialmente entre los empleados más jóvenes, quienes buscan trabajar en empresas con valores alineados a los suyos. ")])]),_c('span',[_vm._v("Desde la perspectiva de los inversionistas, el compromiso ético y la responsabilidad social también generan interés y confianza, ya que reducen los riesgos de reputación y regulaciones a los que están expuestas las empresas. La sostenibilidad y la ética empresarial se han convertido en factores clave para los inversionistas que desean asegurar que sus inversiones están alineadas con sus valores y con una visión de futuro. Esto ha llevado al crecimiento de la inversión socialmente responsable (ISR), donde los fondos de inversión priorizan empresas que cumplen con altos estándares de responsabilidad social y ética.")])]),_c('div',{staticClass:"col-5 d-none d-xl-block",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-21.png"),"alt":"Texto que describa la imagen"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-5 d-none d-xl-block",attrs:{"data-aos":"fade-right"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-22.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-12 col-xl-7",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-4"},[_vm._v("Adoptar un compromiso ético y social requiere superar diversos desafíos internos y externos. En algunos casos, la implementación de políticas de responsabilidad social implica inversiones significativas, como la adopción de prácticas de producción limpia o el desarrollo de programas de apoyo comunitario, que pueden no generar beneficios económicos inmediatos. ")]),_c('div',{staticClass:"bg-verde p-4"},[_c('span',[_vm._v("A nivel organizacional, también es fundamental que estas políticas sean integradas en la cultura corporativa y promovidas por la alta dirección para asegurar su implementación efectiva. Además, la falta de estándares universales en la evaluación de la responsabilidad social y la ética empresarial puede hacer difícil medir de manera precisa el impacto de estas prácticas.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-12 col-md-6 col-lg-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/componentes/material-complementario.svg"),"alt":"Imagen de material complementario"}})])])}]

